
"use client";

import { useState, useEffect, useMemo } from 'react';
import { Download, Mic, Square, Settings, FileText, Languages, Loader2 } from 'lucide-react';
import {
  SidebarProvider,
  Sidebar,
  SidebarInset,
  SidebarHeader,
  SidebarContent,
  SidebarTrigger,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
} from '@/components/ui/sidebar';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from '@/components/ui/dialog';
import { SheetHeader, SheetTitle } from '@/components/ui/sheet';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { useRecorder, type RecordingState } from '@/hooks/use-recorder';
import { exportToSrt } from '@/lib/srt';
import type { TranscriptWord } from '@/lib/types';
import { translateText } from '@/ai/flows/translate-text-flow';
import { transcribeAudio } from '@/ai/flows/speech-to-text-flow';
import { EditorView } from './editor-view';
import { TranscriptView } from './transcript-view';

const blobToBase64 = (blob: Blob): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      if (typeof reader.result === 'string') {
        resolve(reader.result);
      } else {
        reject(new Error('Failed to convert blob to Base64.'));
      }
    };
    reader.onerror = reject;
    reader.readAsDataURL(blob);
  });
};


export function VoiceScribePage() {
  const { recordingState, startRecording, stopRecording, audioBlob, audioUrl, error } = useRecorder();
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [structuredTranscript, setStructuredTranscript] = useState<TranscriptWord[]>([]);
  const [editedTranscript, setEditedTranscript] = useState('');
  const [audioDuration, setAudioDuration] = useState(0);

  const [isTranslating, setIsTranslating] = useState(false);
  const [targetLanguage, setTargetLanguage] = useState('Khmer');
  const [translatedText, setTranslatedText] = useState('');
  const [isTranslationDialogOpen, setIsTranslationDialogOpen] = useState(false);

  const { toast } = useToast();

  useEffect(() => {
    if (error) {
      toast({
        title: 'កំហុសក្នុងការថត',
        description: error,
        variant: 'destructive',
      });
    }
  }, [error, toast]);

  useEffect(() => {
    const processAudio = async () => {
        if (!audioBlob) return;

        const audio = new Audio(URL.createObjectURL(audioBlob));
        audio.onloadedmetadata = () => setAudioDuration(audio.duration);

        setIsTranscribing(true);
        setStructuredTranscript([]);
        setEditedTranscript('');

        try {
            const audioDataUri = await blobToBase64(audioBlob);
            const result = await transcribeAudio({ audioDataUri });
            
            if (result && result.transcript) {
                setStructuredTranscript(result.transcript);
                setEditedTranscript(result.text);
            } else {
                toast({
                    title: "ការចម្លងបានបរាជ័យ",
                    description: "ម៉ូដែលមិនបានផ្តល់នូវការចម្លងទេ។ សូមព្យាយាមម្តងទៀត។",
                    variant: "destructive",
                });
            }
        } catch (e: any) {
            console.error(e);
            toast({
                title: "កំហុសក្នុងការចម្លង",
                description: e.message || "មានបញ្ហាក្នុងការដំណើរការសំឡេងរបស់អ្នក។",
                variant: "destructive",
            });
        } finally {
            setIsTranscribing(false);
        }
    };
    processAudio();
  }, [audioBlob, toast]);

  const handleExport = () => {
    if (editedTranscript && audioDuration > 0) {
      exportToSrt(editedTranscript, audioDuration);
    } else {
      toast({
        title: 'ការនាំចេញបានបរាជ័យ',
        description: 'មិនមានការចម្លងអត្ថបទដើម្បីនាំចេញទេ។',
        variant: 'destructive',
      });
    }
  };

  const handleTranslate = async () => {
    if (!editedTranscript) {
        toast({ title: "គ្មានអ្វីត្រូវបកប្រែទេ", description: "សូមថត ឬបញ្ចូលអត្ថបទជាមុនសិន។", variant: "destructive" });
        return;
    }
    setIsTranslating(true);
    setTranslatedText('');
    setIsTranslationDialogOpen(true);
    try {
        const result = await translateText({ text: editedTranscript, targetLanguage });
        setTranslatedText(result.translatedText);
    } catch (e) {
        toast({ title: "ការបកប្រែបានបរាជ័យ", description: "មិនអាចបកប្រែអត្ថបទបានទេ។ សូម​ព្យាយាម​ម្តង​ទៀត។", variant: "destructive" });
        setIsTranslationDialogOpen(false);
    } finally {
        setIsTranslating(false);
    }
  };


  const recordButtonText = useMemo(() => {
    const stateMap: Record<RecordingState, string> = {
      idle: 'ថត',
      permission_pending: 'កំពុងរង់ចាំ...',
      recording: 'ឈប់',
      stopped: 'ថតម្តងទៀត',
      error: 'ថត'
    };
    return stateMap[recordingState];
  }, [recordingState]);

  const isReadyForContent = !isTranscribing && structuredTranscript.length > 0;

  return (
    <SidebarProvider>
      <Sidebar>
        <SidebarHeader>
            <div className="flex items-center gap-3 p-2">
                <FileText className="text-primary h-6 w-6"/>
                <h2 className="text-xl font-semibold text-sidebar-foreground">VoiceScribe</h2>
            </div>
        </SidebarHeader>
        <SidebarContent>
            <SidebarMenu>
                <SidebarMenuItem>
                    <SidebarMenuButton tooltip="ការកំណត់" isActive>
                        <Settings />
                        <span>ការកំណត់</span>
                    </SidebarMenuButton>
                </SidebarMenuItem>
            </SidebarMenu>
        </SidebarContent>
      </Sidebar>
      <SidebarInset>
        <div className="flex flex-col h-full">
            <header className="flex items-center justify-between p-4 md:p-6 border-b">
                <div>
                    <h1 className="text-2xl font-bold tracking-tight">ស្ទូឌីយោចម្លងសំឡេង</h1>
                    <p className="text-muted-foreground">ថត កែសម្រួល និងនាំចេញអូឌីយ៉ូរបស់អ្នកដោយងាយស្រួល។</p>
                </div>
                <div className="md:hidden">
                    <SidebarTrigger />
                </div>
            </header>
            <main className="flex-grow p-4 md:p-6">
                <Card className="h-full">
                  <CardContent className="h-full p-6">
                    <Tabs defaultValue="edit" className="flex flex-col h-full">
                        <TabsList className="grid w-full grid-cols-2 mb-4">
                            <TabsTrigger value="edit">កម្មវិធីនិពន្ធ</TabsTrigger>
                            <TabsTrigger value="sync">មើលជាមុន</TabsTrigger>
                        </TabsList>
                        {isTranscribing ? (
                            <div className="space-y-4 pt-4 flex-grow flex flex-col items-center justify-center">
                              <Loader2 className="h-12 w-12 animate-spin text-primary" />
                              <p className="text-muted-foreground mt-4">កំពុងចម្លងសំឡេង សូមរង់ចាំ...</p>
                            </div>
                        ) : (
                          <>
                            {structuredTranscript.length === 0 ? (
                                <div className="flex flex-col items-center justify-center text-center flex-grow rounded-lg border-2 border-dashed border-muted-foreground/20 bg-muted/20">
                                    <Mic className="w-20 h-20 text-muted-foreground/30 mb-4"/>
                                    <h3 className="text-2xl font-semibold">ត្រៀមខ្លួនដើម្បីចម្លង</h3>
                                    <p className="text-muted-foreground mt-2">ចុចប៊ូតុងថតនៅខាងស្តាំ ដើម្បីចាប់ផ្តើម។</p>
                                </div>
                            ) : (
                                <>
                                <TabsContent value="edit" className="m-0 flex-grow">
                                    <EditorView
                                        transcript={editedTranscript}
                                        onTranscriptChange={setEditedTranscript}
                                        disabled={!isReadyForContent}
                                    />
                                </TabsContent>
                                <TabsContent value="sync" className="m-0 flex-grow">
                                    <TranscriptView
                                        structuredTranscript={structuredTranscript}
                                        audioUrl={audioUrl}
                                    />
                                </TabsContent>
                                </>
                            )}
                          </>
                        )}
                    </Tabs>
                  </CardContent>
                </Card>
            </main>
        </div>
      </SidebarInset>
      <Sidebar side="right">
        <SidebarHeader>
            <h3 className="text-lg font-semibold text-center text-sidebar-foreground">សកម្មភាព</h3>
        </SidebarHeader>
        <SidebarContent className="p-2 flex flex-col gap-4">
            <Card className="bg-sidebar-accent/50">
                <CardHeader>
                    <CardTitle className="text-base text-center">ការថត</CardTitle>
                </CardHeader>
                <CardContent>
                    <Button 
                        onClick={recordingState === 'recording' ? stopRecording : startRecording} 
                        disabled={recordingState === 'permission_pending' || isTranscribing}
                        className="w-full"
                        size="lg"
                    >
                        {isTranscribing ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : (recordingState === 'recording' ? <Square className="mr-2 h-4 w-4" /> : <Mic className="mr-2 h-4 w-4" />)}
                        {isTranscribing ? 'កំពុងចម្លង...' : recordButtonText}
                    </Button>
                </CardContent>
            </Card>

            <Card className="bg-sidebar-accent/50">
                 <CardHeader>
                    <CardTitle className="text-base text-center">ឧបករណ៍</CardTitle>
                </CardHeader>
                <CardContent className="space-y-3">
                     <Button 
                        onClick={handleExport}
                        variant="outline"
                        disabled={!isReadyForContent}
                        className="w-full bg-sidebar text-sidebar-foreground hover:bg-sidebar/80"
                    >
                        <Download className="mr-2 h-4 w-4" />
                        នាំចេញជា SRT
                    </Button>
                    <div className="space-y-2 pt-2">
                        <h4 className="text-sm font-medium text-center text-muted-foreground">ការបកប្រែ</h4>
                        <Select value={targetLanguage} onValueChange={setTargetLanguage} disabled={!isReadyForContent}>
                            <SelectTrigger>
                                <SelectValue placeholder="ជ្រើសរើស​ភាសា" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem value="Khmer">ភាសាខ្មែរ</SelectItem>
                                <SelectItem value="Spanish">ភាសាអេស្ប៉ាញ</SelectItem>
                                <SelectItem value="French">ភាសាបារាំង</SelectItem>
                                <SelectItem value="German">ភាសាអាល្លឺម៉ង់</SelectItem>
                                <SelectItem value="Japanese">ភាសាជប៉ុន</SelectItem>
                                <SelectItem value="Mandarin">ភាសាចិន​ម៉ាន់ដារ៉ាំង</SelectItem>
                            </SelectContent>
                        </Select>
                        <Button 
                            onClick={handleTranslate}
                            variant="outline"
                            disabled={!isReadyForContent || isTranslating}
                            className="w-full bg-sidebar text-sidebar-foreground hover:bg-sidebar/80"
                        >
                            {isTranslating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Languages className="mr-2 h-4 w-4" />}
                            បកប្រែ
                        </Button>
                    </div>
                </CardContent>
            </Card>
        </SidebarContent>
      </Sidebar>
      <Dialog open={isTranslationDialogOpen} onOpenChange={setIsTranslationDialogOpen}>
        <DialogContent className="sm:max-w-[625px]">
          <DialogHeader>
            <DialogTitle>ការបកប្រែទៅ {targetLanguage}</DialogTitle>
            <DialogDescription>
              អត្ថបទដែលបានចម្លងត្រូវបានបកប្រែ។ អ្នកអាចចម្លងវាពីប្រអយអត្ថបទខាងក្រោម។
            </DialogDescription>
          </DialogHeader>
          <div className="py-4">
            {isTranslating ? (
                <div className="flex items-center justify-center h-48">
                    <Loader2 className="h-8 w-8 animate-spin text-primary" />
                </div>
            ) : (
                <ScrollArea className="h-48 w-full rounded-md border p-4">
                    {translatedText}
                </ScrollArea>
            )}
          </div>
        </DialogContent>
      </Dialog>
    </SidebarProvider>
  );
}
